/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"If I Could Turn Back Time","date":"20 Sep 2001","slug":"/2001-09-20-if-i-could-turn-back-time/","tags":"Y2001"},"html":"<p>I had put a candle instead of a flag on this website. American flags can be a little intimidating outside the US, even to friends. But now I like the <a href=\"http://www.nytimes.com/2001/09/23/magazine/23MYODA.html\">towers of light</a> even better.</p>"},"next":{"fields":{"title":"Nimda Simba","date":"19 Sep 2001","slug":"/2001-09-19-nimda-simba/"}},"previous":{"fields":{"title":"After The Day is Gone","date":"22 Sep 2001","slug":"/2001-09-22-after-the-day-is-gone/"}}},{"node":{"fields":{"title":"Nimda Simba","date":"19 Sep 2001","slug":"/2001-09-19-nimda-simba/","tags":"Y2001"},"html":"<p>AOL/Time/Warner/Netscape/CNN is a worldwide media company, so they have to twist the news differently for different parts of the world.</p>\n<p>In the <a href=\"http://www.cnn.com/\">US</a> <img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/09/newwar.gif\" height=\"26\" width=\"270\" border=\"0\" alt=\"new.war.gif: \" /></p>\n<p>In <a href=\"http://europe.cnn.com/\">Europe</a> <img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/09/warterror.gif\" height=\"19\" width=\"211\" border=\"0\" alt=\"war.terror.gif: \" /></p>\n<table cellspacing=\"0\" border=\"0\" cellpadding=\"0\">\n<tr>\n<td>In&nbsp;<a href=\"http://asia.cnn.com/\">Asia</a> </td>\n<td valign=\"top\" bgcolor=\"#666666\"><font color=\"white\"><b>&nbsp;WAR&nbsp;AGAINST&nbsp;TERRORISM&nbsp;</b></font></td>\n</tr>\n</table>\n<p>I guess if the war is going to be fought in your backyard, you don't get a special graphic.</p>\n<p>After <a href=\"http://editorial.inessential.com/discuss/msgReader$1593?mode=day\">gloating</a> on <a href=\"http://inessential.com\">inessential</a> that the <a href=\"http://www.incidents.org/react/nimda.php\">Nimda worm</a> had nothing to do with me, since I work at a unix firm, I ended up spending the whole day having to do with it... or more correctly, making sure it had nothing to do with us. On the web side, a <a href=\"http://www.squid-cache.org\">squid proxy server</a> can be <a href=\"http://www.ix.de/newsticker/data/ju-18.09.01-001/\">configured to keep it out</a>. Nimda is a DoB (Denial of Bandwidth) attack, which in the end translates into DoM (Denial of Money).</p>\n<p>Meanwhile <a href=\"http://www.freebsd.org/releases/4.4R/announce.html\">FreeBSD 4.4</a> and <a href=\"http://www.kde.org/announcements/announce-2.2.1.html\">KDE 2.2.1</a> are out, in case the Nimdas aren't taking up all your bandwidth.</p>"},"next":{"fields":{"title":"Move Along","date":"18 Sep 2001","slug":"/2001-09-18-move-along/"}},"previous":{"fields":{"title":"If I Could Turn Back Time","date":"20 Sep 2001","slug":"/2001-09-20-if-i-could-turn-back-time/"}}},{"node":{"fields":{"title":"Move Along","date":"18 Sep 2001","slug":"/2001-09-18-move-along/","tags":"Y2001"},"html":"<p>Mama's flying to Munich and back today. We'll see if she runs into any new security procedures. <b>Later:</b> She didn't report anything unusual, except that on the place she sat next to a nice American man with a German wife and a 6-month-old son named Christopher Paul.</p>\n<p>CNN: <a href=\"http://europe.cnn.com/2001/WORLD/europe/09/18/inv.hamburg.suspects/index.html\">Hamburg cell reveals details</a>. Looking at the pictures and the name that one of the hijackers used while in Hamburg, we realize that it's possible we might have had (slight) contact with him a couple of years ago.</p>\n<p>Server change! Not for this website, but for our personal domains, <a href=\"http://photos.shcon.com\">photos</a> and mail. My old hoster is going out of business. If I may pat myself on the back, the main domain is already done and took less than an hour. The trick was that I had the DNS at <a href=\"http://www.easydns.com\">EasyDNS</a>. Once I knew the new IP address, I just changed it on their web interface and it was active within 15 minutes. So no horror story of being unreachable for days!</p>\n<p>FWIW, my domains are now at <a href=\"http://www.pair.com\">Pair</a>, where I've done business before. FreeBSD, SSH access, and they got the site going within a couple hours of my online application.</p>"},"next":{"fields":{"title":"World in a Stupor","date":"17 Sep 2001","slug":"/2001-09-17-world-in-a-stupor/"}},"previous":{"fields":{"title":"Nimda Simba","date":"19 Sep 2001","slug":"/2001-09-19-nimda-simba/"}}}],"pathPrefix":"","first":false,"last":false,"index":974,"pageCount":1101}}

/***/ })

